@import url('https://fonts.googleapis.com/css2?family=Josefin+Sans:ital,wght@0,200;1,100;1,300&display=swap');


body {
  font-family: 'Josefin Sans', sans-serif;
  background-color: beige;
  margin: 0;
}

/* loading bar center align */
.center {
  width: 100%;
  height: 50vw;
  display: flex;
  align-items: center;
  justify-content: center;
}

/* each movie box style */
.movieBox {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
  padding: 100px 120px;
}

a {
  text-decoration: none;
  color: #333;
}

.topTitle {
  padding-top:50px;
  margin-bottom: 0;
  text-align: center;
}

.movie {
  /* background-color: beige; */
  background-color: #00008B;
  background-color: #FFE77A;

  display: flex;
  width: 420px;
  border-radius: 18px;
  margin-bottom: 32px;
  box-shadow: 5px 5px 5px rgb(70, 69, 69);
  transition: .5s;
}

.movie:hover {
  /* background-color: rgb(149, 255, 0); */
  background-color: #ffbeb8;
  transform: scale(1.02);
}

.movie > div > img {
  max-width: 160px;
  padding: 20px 0 20px 20px;
}

.description {
  width: 100%;
  margin: 18px;
  position: relative;
}

.description > h3 {
  width: 200px;
  
  text-overflow: ellipsis; 
  white-space: nowrap;
  overflow: hidden;
}

.description div:last-child {
  margin-top: 5px;
}

.summary {
  font-size: 14px;
  max-height: 70px;
  max-width: 200px;
  /* overflow: scroll; */
  /* border: 1px dotted rgb(99, 94, 92); */
  border-radius: 8px;
  padding: 7px;
}

.summary::-webkit-scrollbar {
  display: none;
}

.movieInfos {
  padding-left: 7px;
  font-size: 13px;
}

.genres {
  padding-left: 0;
  display: flex;
  margin-bottom: 5px;
  margin-left: 7px;
  width: 200px;
  white-space: nowrap;
  text-overflow: ellipsis; 
  overflow: hidden;
}

li {
  list-style: none;
}

/* movie details */

.genre:not(:last-child)::after {
  content: ", \00a0"
}

.detailContainer {
  display: flex;
  justify-content: center;
  height: 100vh;
  background-repeat: no-repeat;
    background-size: cover;
}


  .detailBox {
    display: flex;
    margin-top: 110px;
    background-color: #FFE77A;
    width: 1300px;
    box-shadow: 3px 3px 3px rgb(75, 73, 73);
    border-radius: 5px;
    height: 700px;
  }

  .imageBox {
    padding: 60px;
  }


/* movie title */
.detailBox h1 {
    text-align: center;
    margin: 60px;
  }

  .detailBox>div>img {
    width: 380px;
    border-radius: 20px;
    box-shadow: 8px 5px 5px rgb(75, 73, 73);
  }


.detailInfos {
  margin: 5%;
  font-size: 22px;
}

.description {
  margin: 12px 12px 0 0 ;
  height: 222px;
  padding: 8px;
}

.plot {
  max-height: 190px;
  border: dotted 2px rgb(212, 173, 110);
  border-radius: 8px;
  box-shadow: 3px 3px 3px rgb(32, 31, 31);
  overflow: scroll;
  padding: 10px;
}

.plot::-webkit-scrollbar {
  width: 5px; /*스크롤바의 너비*/
  color: blue;
}
.plot::-webkit-scrollbar-thumb {
  background-color: rgb(21, 87, 5); /*스크롤바의 색상*/
}


.description > h3 {
  padding-left: 7px;
}

.description > h3:hover {
  text-decoration: underline;
}


button {
  cursor: pointer;
  border: none;
  height: 34px;
  width: 150px;
  border-radius: 8px;
  background-color: #ffbeb8;
  box-shadow: 3px 3px 3px grey;
  transition: .3s;
  font-size: 85%;
}

button:hover {
  transform: scale(1.03);
}